import React from 'react';
import { Row, Col, Container } from 'reactstrap';
import { List } from './model/models';
import { connect } from 'react-redux';
import { State } from './redux/store';
import TodoList from './component/TodoList';
import ListOfTodoLists from './component/ListOfTodoLists';
import packageJson from '../package.json';

interface Props {
    activeList: List
    lists: List[]
}

function mapStateToProps(state: State) {
    return {
        activeList: state.lists.filter(list => list.id === state.activeListId)[0],
        lists: state.lists
    }
}

class App extends React.Component<Props> {

    render() {
        return (
            <div className="h-100 d-flex flex-column justify-content-between" >
                <Container className="mt-4 flex-grow-1">
                    <Row>
                        <Col lg={7}>

                            <TodoList list={this.props.activeList} />

                        </Col>
                        <Col lg={5} className="mb-4 order-first">

                            <ListOfTodoLists activeListId={this.props.activeList.id}
                                lists={this.props.lists} />

                        </Col>
                    </Row>
                </Container>
                <div className="d-flex flex-shrink-0 mb-3 justify-content-center">
                    <small style={{ color: "#c3c3c3" }}>v{packageJson.version} </small>
                </div>
            </div>
        )
    }
}

export default connect(mapStateToProps)(App)
